import React from "react";
import Navbar from "./Navbar";
import Image from "./Image";
import imgData from "../url/url";
import "../styles/main.css";

class Counter extends React.Component {
  state = {
    score: 0,
    bestScore: 0,
    imgArr: imgData
  };

  componentDidMount() {
    this.ramdomizeImages();
  }

  handleClickCount = () => {
    console.log("HERE");
    this.ramdomizeImages();
    this.setState({ score: this.state.score + 1 });
  };

  handleTextChange = () => {};

  ramdomizeImages = () => {
    const arr = [];
    while (arr.length < imgData.length) {
      let r = Math.floor(Math.random() * imgData.length);
      if (arr.indexOf(r) === -1) {
        arr.push(imgData[r]);
      }
    }
    console.log("shuffled Array", arr);
    this.setState({ imgArr: arr });
  };

  render() {
    return (
      <div>
        <Navbar score={this.state.score} bestScore={this.state.bestScore} />
        <div className="flex">
          {this.state.imgArr.map(element => (
            <Image
              key={element.src}
              src={element.src}
              alt={element.alt}
              isClicked={element.isClicked}
              onClick={this.handleClickCount}
            />
          ))}
        </div>
      </div>
    );
  }
}

export default Counter;
